<template>
	<div>
		<div class="container center banner">
			<img src="../../static/img/banner2.png" class="img-responsive pc-bg" />
			<img src="../../static/img/m-banner2.png" class="img-responsive m-bg" />
		</div>
		
		<div class="container center info-content">
			<div class="col-lg-3 col-md-3 col-sm-12 col-xs-12">
				<!--pc-->
				<div class="pc-item-nav">
					<div class="zhaosheng">
						<a href="#">
							<router-link to="/sign_online">
								<img src="../../static/img/slide1.png" class="img-responsive" />
							</router-link>
						</a>
						<a href="#">
							<router-link to="/special_major_accouting">
								<img src="../../static/img/slide2.png" class="img-responsive" />
							</router-link>
						</a>
					</div>
				</div>
			</div>
			<div class="col-lg-9 col-md-9 col-sm-12 col-xs-12 info">
				<div class="info-title">
					<h3>招生快讯</h3>
					<div class="info-title-right pc-bg">
						<i class="iconfont icon-zhengfangxing"></i>
						<span><router-link to="/">首页</router-link></span>
						<span>></span>
						<span><router-link to="/register_guide">招生快讯</router-link></span>
					</div>
				</div>
				<div class="col-lg-12 col-md-12 col-sm-12 col-xs-12 content">
					<span id="none">资料正在整理中..</span>
					<div class="list-item" v-for="item in nowList">
						<router-link :to="{name: 'news', params: { id: item.id }}" target="_blank">
							<i class="iconfont icon-triangle-copy-copy-copy"></i> 
							<p>{{item.title}}</p> 
							<span>{{item.add_time}}</span></a>
						</router-link>
					</div>
					
					<!--fenye-->
					<nav class="p-page" aria-label="Page navigation" id="page">
					  <ul class="pagination">
					    <li @click="jump(curPage==1?1:curPage-1)">
					      <a href="#" aria-label="Previous">
					        <span aria-hidden="true">&laquo;</span>
					      </a>
					    </li>
					    <li v-for="item in totalPageList" @click="jump(item)" :class="{active:curPage==item}"><a href="#">{{item}}</a></li>
					    <li @click="jump(curPage==totalPage?totalPage:curPage+1)">
					      <a href="#" aria-label="Next">
					        <span aria-hidden="true">&raquo;</span>
					      </a>
					    </li>
					  </ul>
					</nav>
					<nav class="m-page" aria-label="..." id="page1">
					  <ul class="pager">
					    <li @click="jump(curPage==1?1:curPage-1)"><a href="#">&laquo;上一页</a></li>
					    <li @click="jump(curPage==totalPage?totalPage:curPage+1)"><a href="#">下一页&raquo;</a></li>
					  </ul>
					</nav>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
	        return{
				newsLiat:[],
				curPage:1,
				total:'',
				show:10,
				nowList:[],
				totalPage:'',
				totalPageList:[],
	        }
	    },
	    created:function(){	
	    	var _self = this;
	    	var url1="http://api.7171.la/api/article";            
			this.$http.post(url1,{token:'gzsgzdzy',code:'18b304b9c103d44eca20b919dc99329e',type:'22'}
				).then(function (data) {
	        	var json = data.data;  
			  	_self.newsLiat = json;
			  	if(data.data.length == 0){
					var none = document.getElementById('none');
					none.style.display = 'block';
					document.getElementById('page').style.display = "none";
					document.getElementById('page1').style.display = "none";
					return false;
				}
			  	_self.total = _self.newsLiat.length;
				//生成多少页
				_self.totalPage = _self.total/_self.show;
				if(_self.totalPage <= 1){
					document.getElementById('page').style.display = "none";
					document.getElementById('page1').style.display = "none";
				}
				for(var i = 0;i<_self.totalPage;i++){
					_self.totalPageList.push(i+1);
				}
				
				//每一页装多少条
				if(_self.total < _self.show)
					_self.show = _self.total
				for(var i =0;i<_self.show;i++){
					_self.nowList.push(_self.newsLiat[i]);
				}
	    	});   
	    },
	    methods:{
	  		jump(page){
				this.curPage = page;
				this.nowList = [];
				var star = (page-1)*this.show;
				var end = (page-1)*this.show+this.show;
				if(end > this.total)
					end = this.total;
				for (var i = star;i<end;i++) {
					this.nowList.push(this.newsList[i]);
				}
			}
	  	},
		activated(){
			 document.documentElement.scrollTop = 0
		},
		beforeRouteEnter (to, from, next) {
			next(vm => {
			    document.title="招生快讯_韶关市中等职业技术学院 （广州校区）重点特色专业网";
		  	});
		}
	}
</script>

<style>
	
</style>


